.tb_reaction {
       position: fixed;
       z-index: 9999999999;
       right: 50px;
       bottom: 30px;
       transition: opacity .2 ease-out;
       opacity: 0;
}



.tb_likes_vew {
       position: relative;
       z-index: 3;
}
.tb_active.tb_reaction{
       opacity: 1;
}
.tb_active .tb_likes_vew {
       animation: tb_sonar_ani 2s;
}

.tb_active .tb_likes_icon {
       animation: tb_beat_ani 1s infinite alternate;
       transform-origin: center;
       animation-delay: 2s;
}

@keyframes tb_sonar_ani_rev {
       0% {
              transform: scale(1);
              opacity: 1;
       }
       100% {
              transform: scale(0);
              opacity: 0;
       }
}
@keyframes tb_sonar_ani {
       0% {
              transform: scale(0);
              opacity: .4;
       }

       50% {
              transform: scale(1.2);
              opacity: 1;
       }

       100% {
              transform: scale(1);
              opacity: 1;
       }
}

@keyframes tb_beat_ani {
       to {
              transform: scale(1.2)
       }
}

.tb_hearts {
       position: absolute;
       bottom: 15px;
       right: 10px;
       height: 50px;
       width: 50px;
}

.tb_hearts .tb_heart {
       position: absolute;
       list-style: none;
       width: 50px;
       height: 50px;
       margin: 4px;
}

.tb_hearts .tb_heart:before,
.tb_hearts .tb_heart:after {
       position: absolute;
       content: "";
       left: 16px;
       top: 0;
       width: 16px;
       height: 24px;
       background: red;
       -moz-border-radius: 16px 16px 0 0;
       -o-border-radius: 16px 16px 0 0;
       border-radius: 16px 16px 0 0;
       -webkit-transform: rotate(-45deg);
       -moz-transform: rotate(-45deg);
       -o-transform: rotate(-45deg);
       transform: rotate(-45deg);
       -webkit-transform-origin: 0 100%;
       -moz-transform-origin: 0 100%;
       -o-transform-origin: 0 100%;
       transform-origin: 0 100%;
}

.tb_hearts .tb_heart:after {
       left: 0;
       -webkit-transform: rotate(45deg);
       -moz-transform: rotate(45deg);
       -o-transform: rotate(45deg);
       transform: rotate(45deg);
       -webkit-transform-origin: 100% 100%;
       -moz-transform-origin: 100% 100%;
       -o-transform-origin: 100% 100%;
       transform-origin: 100% 100%;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(even) {
       animation: 3s tb_flyingEven_ani linear infinite;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(odd) {
       animation: 3s tb_flyingOdd_ani linear infinite;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(1) {
       animation-delay: 0.15s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(2) {
       animation-delay: 0.3s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(3) {
       animation-delay: 0.45s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(4) {
       animation-delay: 0.6s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(5) {
       animation-delay: 0.75s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(6) {
       animation-delay: 0.90s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(7) {
       animation-delay: 1.05s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(8) {
       animation-delay: 1.20s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(9) {
       animation-delay: 1.35s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(10) {
       animation-delay: 1.5s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(11) {
       animation-delay: 1.65s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(12) {
       animation-delay: 1.7s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(13) {
       animation-delay: 1.8s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(14) {
       animation-delay: 2s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(15) {
       animation-delay: 2.3s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(16) {
       animation-delay: 2.5s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(17) {
       animation-delay: 2.4s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(18) {
       animation-delay: 2.5s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(19) {
       animation-delay: 2.55s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(20) {
       animation-delay: 2.7s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(20) {
       animation-delay: 2.85s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(20) {
       animation-delay: 2.9s;
}

.tb_hearts.tb__flying>.tb_heart:nth-child(20) {
       animation-delay: 3s;
}


@keyframes tb_flyingEven_ani {
       0% {
              transform: translate(5px, 10px) scale(1);
              opacity: 1;
       }

       70% {
              opacity: 0;
       }

       100% {
              transform: translate(0px, -90vh) scale(1.5);
              opacity: 0;
       }
}


@keyframes tb_flyingOdd_ani {
       0% {
              transform: translate(0, 0) scale(1);
              opacity: 1;
       }

       70% {
              opacity: 0;
       }

       100% {
              transform: translate(-60px, -90vh) scale(1.5);
              opacity: 0;
       }
}

.tb_like_ico {
       position: absolute;
       right: 8px;
       bottom: 8px;
       z-index: 8;
       font-size: 24px;
       background-color: rgba(255, 255, 255, 0.9);
       border-radius: 50%;
       padding: 7px;
       height: 35px;
       width: 35px;
       display: flex;
       align-items: center;
       justify-content: center;
       box-shadow:0px 1px 2px rgba(0, 0, 0, 0.2);
}

.tb_like_ico .tb__icon {
       color: #000;
       transition: all .3s ease;
       font-size: 20px;
       margin-top: 2px;
}


.tb_animate .tb__icon{
       animation:tb_like_ani 0.5s 1;
       color: red;
}

@keyframes tb_like_ani {
       0%   { transform: scale(1); }
       90%   { transform: scale(1.2); }
       100% { transform: scale(1.1); }
}